import * as React from "react"
import { StaticImage } from "gatsby-plugin-image"

import Layout from "../components/layout"
import Seo from "../components/seo"

import logoSequia from "../images/logo-sequoia.png"
import logoKleiner from "../images/logo-kleiner-perkins.png"
import logoNu from "../images/logo-nu-bank.png"
import Tania from "../images/tania.png"
import Khurram from "../images/khurram.png"

import { BsLinkedin } from "react-icons/bs"
import { BsTwitter } from "react-icons/bs"
import { BiCheckCircle } from "react-icons/bi"

// const data = {
