const IndexPage = () => {
  function onFormSubmit(e) {
    e.preventDefault()

    const form = e.target
    const allFormFields = [...form.elements]

    let formSuccess = [...form.getElementsByClassName("form-submitted")]

    let formData = new FormData()

    allFormFields.forEach(field => {
      switch (field.type) {
        case "submit":
          field.disabled = true
          break
        default:
          field.disabled = true
          formData.append(field.name, field.value)
      }
    })

    console.log("prevention", formData, formSuccess)

    // formData.append("Created", e.target.Created.value)
    // formData.append("User Feedback", e.target.userFeedback.value)

    fetch(e.target.action, {
      method: "POST",
      // headers: {
      //   "Content-Type": "application/x-www-form-urlencoded",
      // },
      body: formData,
    })
      .then(res => {
        // success
        allFormFields.forEach(field => {
          field.classList.add("d-none")
        })
        formSuccess.forEach(node => {
          node.classList.toggle("d-none")
        })
        form.reset()
      })
      .catch(err => {
        // failure
        allFormFields.forEach(field => {
          field.disabled = false
        })
      })
  }

  return (
    <Layout>
      <Seo title="Home" />

      <section id="hero" className="bg-primary text-white position-relative">
        <svg
          className="hero-insignia position-absolute"
          width="749"
          height="438"
          viewBox="0 0 749 438"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            className="rellax"
            data-rellax-speed="2"
            d="M102.352 233.358L281.613 -76.1518L284.716 -81.4913L330.196 -160H242.605L197.125 -81.4913L194.066 -76.1518L102.352 82.2231L76.1992 127.383L25.444 214.986L0 258.924H87.4584L102.352 233.358Z"
            fill="white"
          />
          <path
            className="rellax"
            data-rellax-speed="3"
            d="M459.855 -81.4913L505.379 -160H417.788L372.308 -81.4913L369.205 -76.1518L120.438 353.451L117.246 358.927L71.7663 437.3H159.358L204.838 358.927L208.029 353.451L456.796 -76.1518L459.855 -81.4913Z"
            fill="white"
          />
          <path
            className="rellax"
            data-rellax-speed="2"
            d="M586.144 -148.371L547.446 -81.4915L542.393 -72.6678C640.844 -54.8393 715.714 33.0814 715.714 138.65C715.714 257.069 621.34 353.496 505.335 353.496H295.577L292.385 358.926L246.905 437.299H505.335C666.953 437.299 797.853 303.54 797.853 138.604C797.853 2.26617 708.444 -112.714 586.144 -148.371Z"
            fill="white"
          />
        </svg>
        <div className="container" id="hero-content">
          <div className="row">
            <div className="col-12 col-md-9 col-lg-6">
              <h1>
                Making it simple to <strong>borrow</strong>,{" "}
                <strong>transact</strong> and <strong>save</strong> money
                securely.
              </h1>
              <div className="">
                <form
                  action="https://api.sheetmonkey.io/form/7QhNyAuD5UnuC69NXEYp1v"
                  method="post"
                  onSubmit={onFormSubmit}
                  submitted="false"
                >
                  <h6 className="fw-normal form-submitted">Be in the know</h6>
                  <div className="d-md-flex">
                    <input
                      type="email"
                      name="Email"
                      required
                      className="form-control"
                      placeholder="example@gmail.com"
                    />
                    <input
                      type="hidden"
                      name="Created"
                      value="x-sheetmonkey-current-date-time"
                    />
                    <button
                      className="btn btn-dark flex-shrink-0 mt-2 mt-md-0 ms-md-2"
                      type="submit"
                    >
                      <span
                        className="spinner spinner-border spinner-border-sm me-2"
                        role="status"
                      >
                        <span className="visually-hidden">Loading...</span>
                      </span>
                      Sign up
                    </button>
                  </div>
                  <div className="form-submitted d-none mt-3 mt-sm-2">
                    <div className="d-flex text-white align-items-start">
                      <BiCheckCircle
                        size={`1.25rem`}
                        style={{ marginTop: 2 }}
                      />
                      <p className="ms-1 mb-0">
                        <span className="fw-bold">
                          Thanks for sharing your email!
                        </span>{" "}
                        <br />
                        <span className="">
                          We’ll send you Dbank updates periodically.
                        </span>
                      </p>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </section>

      <section id="access" className="pt-5 pt-lg-7 pb-5 pb-lg-6">
        <div className="container">
          <div className="row align-items-center">
            <div className="col-12 col-lg-6">
              <div data-sal="slide-up" data-sal-duration="600">
                <StaticImage
                  className="img-fluid w-100 rounded"
                  src="../images/section-one-image.jpg"
                  loading="eager"
                  width={505}
                  quality={95}
                  formats={["auto", "webp", "avif"]}
                  alt="Women"
                  style={{
                    marginBottom: `var(--space-3)`,
                    aspectRatio: `505 / 336`,
                  }}
                />
              </div>
            </div>
            <div className="col-lg-1"></div>
            <div className="col-12 col-lg-5">
              <h2 className="mb-3 mt-3 mt-lg-0 mb-lg-4">
                Access to financial <br />
                services for everyone
              </h2>
              <p>
                Dbank dreams of a world where everyone - whether they are a
                young graduate in Lahore, a homemaker in Karachi, a community
                health care worker in Peshawar or a small business owner in
                Quetta - has access to financial services in the Pan-Islamic
                world, beginning with Pakistan.
              </p>
              <hr className="mt-5 d-lg-none" />
            </div>
          </div>
        </div>
      </section>

      <section id="about" className="pb-5 pb-lg-6">
        <div className="container">
          <div className="row">
            <div
              id="about-logos"
              className="py-lg-5 col-12 col-lg-6 order-lg-2 d-flex align-items-center justify-content-between flex-column"
            >
              <img
                data-sal="zoom-in"
                data-sal-duration="600"
                className="m-lg-0"
                src={logoSequia}
                alt="Logo"
                id="logo-sequia"
              />
              <img
                data-sal="zoom-in"
                data-sal-duration="600"
                data-sal-delay="200"
                className="m-lg-0"
                src={logoKleiner}
                alt="Logo"
                id="logo-kleiner"
              />

              <img
                data-sal="zoom-in"
                data-sal-duration="600"
                data-sal-delay="400"
                className="m-lg-0"
                src={logoNu}
                alt="Logo"
                id="logo-nu"
              />
            </div>
            <div
              id="about-text"
              className="col-12 col-lg-6 order-lg-0 py-lg-5 pe-lg-7 mt-4 mt-lg-0"
            >
              <h2>News at Dbank</h2>
              <p>
                Dbank raised <strong>17.6 million dollars</strong> in the seed
                round led by Sequoia Capital Southeast Asia and Kleiner Perkins.{" "}
                <span className="d-block"></span>
                Strategic investors include Nubank, local player Askari Bank and
                Rayn. The seed round also includes participation from RTP Global
                and angel investors who have worked at companies such as Google,
                Goldman Sachs and Stripe.
              </p>
            </div>
          </div>
        </div>
      </section>

      <section id="founders" className="py-5 py-lg-6 bg-info">
        <div className="container">
          <div className="row">
            <div className="col-12">
              <h2 className="mb-0">Co-founders</h2>
            </div>
          </div>
          <div className="row">
            <div className="col-12">
              <div id="profile-cards">
                <div
                  className="profile-card"
                  data-sal="slide-up"
                  data-sal-duration="600"
                >
                  <div className="profile-picture" style={{ color: "#9fb4ff" }}>
                    <img src={Tania} alt="Tania Aidrus" />
                  </div>
                  <div className="profile-text">
                    <div>
                      <h5>Tania Aidrus</h5>
                      <p>
                        Tania previously served as an advisor to the Prime
                        Minister of Pakistan, where she put in place a framework
                        for the country's digitisation journey. Before this,
                        Tania was at Google for over a decade, where she was
                        focused on helping move economies from cash to digital.
                      </p>
                    </div>
                    <div className="socials">
                      <a
                        className="hover-scale"
                        target="_blank"
                        href="https://www.linkedin.com/in/tania-aidrus-b68607/"
                      >
                        <BsLinkedin />
                      </a>
                      <a
                        target="_blank"
                        href="https://twitter.com/taidrus"
                        className="ms-2 ms-lg-0 hover-scale"
                      >
                        <BsTwitter />
                      </a>
                    </div>
                  </div>
                </div>
                <div
                  className="profile-card"
                  data-sal="slide-up"
                  data-sal-duration="600"
                  data-sal-delay="300"
                >
                  <div className="profile-picture" style={{ color: "#aafae3" }}>
                    <img
                      src={Khurram}
                      alt="Khurram Jamali"
                      id="picture-khurram"
                    />
                  </div>
                  <div className="profile-text">
                    <div>
                      <h5>Khurram Jamali</h5>
                      <p>
                        Khurram was the Head of Partnerships for Payments across
                        the Next Billion User (NBU) markets for Google, he has
                        witnessed the remarkable results of financial inclusion
                        through the spread of digital payments in developing
                        countries. He led the Pakistani market at Google
                        Singapore, growing the business 10x in less than five
                        years.
                      </p>
                    </div>
                    <div className="socials">
                      <a
                        className="hover-scale"
                        target="_blank"
                        href="https://www.linkedin.com/in/khurram-khalil-jamali-%E5%BC%A0%E5%8F%AF%E5%AE%B9-%D8%AE%D8%B1%D9%91%D9%85-%D8%AE%D9%84%DB%8C%D9%84-%D8%AC%D9%85%D8%A7%D9%84%DB%8C-75284318/"
                      >
                        <BsLinkedin />
                      </a>
                      <a
                        target="_blank"
                        href="https://twitter.com/kjamali"
                        className="ms-2 ms-lg-0 hover-scale"
                      >
                        <BsTwitter />
                      </a>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>

      <section id="form" className="py-6">
        <div className="container">
          <div className="row">
            <div className="col-12 col-lg-5">
              <h2 className="display-4">
                Tell us one thing you wish <br className="d-none d-xl-block" />
                your bank did better
              </h2>
            </div>
            <div className="col-12 col-lg-7">
              <form
                action="https://api.sheetmonkey.io/form/t2cdqyEukVqTJUgiHR27LR"
                method="post"
                onSubmit={onFormSubmit}
                submitted="false"
              >
                <textarea
                  name="User Feedback"
                  placeholder="I wish my bank would..."
                  cols="30"
                  rows="5"
                  required
                  className="form-control mb-3"
                ></textarea>
                <input
                  type="hidden"
                  name="Created"
                  value="x-sheetmonkey-current-date-time"
                />
                <button className="btn btn-primary" type="submit">
                  <span
                    className="spinner spinner-border spinner-border-sm me-2"
                    role="status"
                  >
                    <span className="visually-hidden">Loading...</span>
                  </span>
                  Submit
                </button>
                <div className="form-submitted d-none">
                  <div className="d-flex text-primary align-items-start">
                    <BiCheckCircle size={`1.25rem`} style={{ marginTop: 2 }} />
                    <p className="ms-1 mb-0">
                      <span className="fw-bold">
                        Thank you for your feedback!
                      </span>{" "}
                      <br />
                      <span className="">
                        We’re working hard to revolutionize banking in Pakistan.
                      </span>
                    </p>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </section>
    </Layout>
  )
}

export default IndexPage
